import React from 'react';
import { NavLink } from 'react-router-dom';
import classes from './Navlink.module.scss'

const Navlink = () => {
    return (
        <nav className={classes.nav_block}>
            <div className={classes.nav_link}>
                <NavLink to='/1'  className={({ isActive }) => (isActive ? classes.active : '')} >Смартфони та електроніка</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/2'  className={({ isActive }) => (isActive ? classes.active : '')}>Ноутбуки та комп’ютери</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/3' className={({ isActive }) => (isActive ? classes.active : '')}>Побутова техніка</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/4' className={({ isActive }) => (isActive ? classes.active : '')}>Товари для дому</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/5' className={({ isActive }) => (isActive ? classes.active : '')}>Товари для геймерів</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/6' className={({ isActive }) => (isActive ? classes.active : '')}>Інструменти та автотовари</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/7' className={({ isActive }) => (isActive ? classes.active : '')}>Дитячі товари</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/8' className={({ isActive }) => (isActive ? classes.active : '')}>Зоотовари</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/9' className={({ isActive }) => (isActive ? classes.active : '')}>Офіс, школа, книги</NavLink>
            </div>
            <div className={classes.nav_link}>
                <NavLink to='/10' className={({ isActive }) => (isActive ? classes.active : '')}>Розпродаж до -70%</NavLink>
            </div>
          
      </nav>
    )
}

export default Navlink;